.Portfolio{
    height: 100%;
    background-color: var(--pagesBackgroundColor);
    width: 100%;
    height: 100%;
    padding-left: 3%;
    padding-right: 3%;

}

.categoriesHolder{
    float: left;
    height: 68%;
    width: 100%;
    overflow: hidden;
    margin-bottom: 3%;
}
    .categoryHolder{

        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        overflow: hidden;
    }
        .moveLeft{
            height: 100%;
            width: 5%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .ProductsContainer{
            height: 100%;
            width: 90%;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            overflow: hidden;
        }
            .productContainer{
                width: 100%;
                height: 100%;
            }
        .moveRight{
            height: 100%;
            width: 5%;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .nextImage{
            width: 100%;
        }

.tabsMenu{
    height: 8%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-top: 0px double var(--primary);
    border-bottom: 0px double var(--primary);
}

    .tab{
        height: 100%;
        display: flex;
        align-items: center;
        justify-items: center;
        justify-content: space-around;
    }

    a.tabEntry{
        color: black;
        height: 100%;
        display: flex;
        align-items: center;
        text-decoration: none;
        font-size: 1.5vw;
    }



@media (pointer:none), (pointer:coarse), (max-width: 600px) {
    .Portfolio{
        padding-left: 1%;
        padding-right: 1%;

    }


    .categoriesHolder{
        height: 70%;
    }
    .categoryHolder{

    }
    .moveLeft{
        width: 0%;
    }


    .ProductsContainer{
        overflow: scroll;
        width: 100%;
        height: 100%;

    }
        .productContainer{
            padding-right: 5%;
            padding-left: 5%;
        }

    .moveRight{
        width: 0%;
    }
    .nextImage{
        width: 100%;
    }

    .tabsMenu{
        height: 10%;
        width: 100%;
    }

    .tab{
        width: 25%;
    }

    .tabEntry{
        height: 100%;
        display: flex;
        align-items: center;
        text-decoration: none;
        width: 25%;

    }
    .portfolioNavIcon{

    }
    .porfolioNavA{
        display: none;
    }
}